  height: 10px;
  width: 10px;
  border-radius: 100%;
  background-color: #45eeff;
  -webkit-animation: orbit 10s linear infinite;
          animation: orbit 10s linear infinite;
  cursor: pointer;
}

/*Mars*/
.mars {
  position: relative;
  top: 95px;

  height: 10px;
  width: 10px;
  border-radius: 100%;
  background-color: #ff4e4e;
  -webkit-animation: orbit3 15s linear infinite;
          animation: orbit3 15s linear infinite;
  cursor: pointer;
}

