/*Hearth*/
@-webkit-keyframes orbit {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(75px);
            transform: translate(0%, -0%) rotate(0deg) translateX(75px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(75px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(75px);
  }
}
@keyframes orbit {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(75px);
            transform: translate(0%, -0%) rotate(0deg) translateX(75px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(75px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(75px);
  }
}

/*Jupiter*/
@-webkit-keyframes orbit2 {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(110px);
            transform: translate(0%, -0%) rotate(0deg) translateX(110px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(110px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(110px);
  }
}
@keyframes orbit2 {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(110px);
            transform: translate(0%, -0%) rotate(0deg) translateX(110px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(110px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(110px);
  }
}

/*Mars*/
@-webkit-keyframes orbit3 {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(90px);
            transform: translate(0%, -0%) rotate(0deg) translateX(90px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(90px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(90px);
  }
}
@keyframes orbit3 {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(90px);
            transform: translate(0%, -0%) rotate(0deg) translateX(90px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(90px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(90px);
  }
}

@-webkit-keyframes orbitGalilean {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(15px);
            transform: translate(0%, -0%) rotate(0deg) translateX(15px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(15px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(15px);
  }
}

@keyframes orbitGalilean {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(15px);
            transform: translate(0%, -0%) rotate(0deg) translateX(15px);
  }
  to {
    -webkit-transform: translate(-0%, -0%) rotate(360deg) translateX(15px);
            transform: translate(-0%, -0%) rotate(360deg) translateX(15px);
  }
}

@-webkit-keyframes orbitLunar {
  from {
    -webkit-transform: translate(0%, -0%) rotate(0deg) translateX(13px);
            transform: translate(0%, -0%) rotate(0deg) translateX(13px);
  }
  to {
